import React, { useState } from 'react';

const OnboardingForm = () => {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    tierType: '',
  });
  const [errors, setErrors] = useState({
    firstName: '',
    lastName: '',
    email: '',
    tierType: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevFormData) => ({
      ...prevFormData,
      [name]: value,
    }));
    setErrors((prevErrors) => ({
      ...prevErrors,
      [name]: '',
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // Simple validation
    let formIsValid = true;
    const newErrors = { ...errors };

    if (formData.firstName.trim() === '') {
      formIsValid = false;
      newErrors.firstName = 'First name is required';
    }

    if (formData.lastName.trim() === '') {
      formIsValid = false;
      newErrors.lastName = 'Last name is required';
    }

    if (formData.email.trim() === '') {
      formIsValid = false;
      newErrors.email = 'Email is required';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      formIsValid = false;
      newErrors.email = 'Invalid email format';
    }

    if (formData.tierType.trim() === '') {
      formIsValid = false;
      newErrors.tierType = 'Tier type is required';
    }

    setErrors(newErrors);

    if (formIsValid) {
      // Form is valid, save the data to local storage
      localStorage.setItem('formData', JSON.stringify(formData));
      console.log('Form data saved:', formData);
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <div>
      {errors.firstName && <div className="error">{errors.firstName}</div>}
        <label>
          First Name<br/>
          <input
            type="text"
            name="firstName"
            value={formData.firstName}
            onChange={handleChange}
          />
        </label>
      </div>
      <div>
      {errors.lastName && <div className="error">{errors.lastName}</div>}
        <label>
          Last Name<br/>
          <input
            type="text"
            name="lastName"
            value={formData.lastName}
            onChange={handleChange}
          />
        </label>
        
      </div>
      <div>
        {errors.email && <div className="error">{errors.email}</div>}
        <label>
          Email<br/>
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
          />
        </label>
      </div>
      <div>
      {errors.selection && <div className="error">{errors.selection}</div>}

        <label>
          Select tier<br/>
          <select
            name="tiers"
            value={formData.selection}
            onChange={handleChange}
          >
            <option value="">Select a savings tier</option>
            <option value="tier_1">Tier 1</option>
            <option value="tier_2">Tier 2</option>
            <option value="tier_3">Tier 3</option>
          </select>
        </label>
      </div>
      <button type="submit">Submit</button>
    </form>
  );
};

export default OnboardingForm;
